import { ref } from 'vue';
import * as XLSX from 'xlsx';
import { useStore } from 'vuex';

export function useStudentReport() {
    const excelDataTemp = ref([]);
    const excelDataOriginal = ref([]);
    const filteredItems = ref([]);

    const store = useStore();

    // Configuracin de fechas segn el cdigo original
    const reportDate = '2025-07-01'; // Fecha del reporte
    const reportDateObj = new Date(reportDate);
    const finalDate = new Date(reportDateObj.getFullYear(), reportDateObj.getMonth() + 1, 0); // Fecha Final
    const initialDate = new Date(finalDate);
    initialDate.setMonth(initialDate.getMonth() - 11); // Restar 11 meses
    initialDate.setDate(0); // ltimo da del mes

    // Funcin para leer el archivo Excel
    const loadExcel = async() => {
        const defaultFilePath =
            process.env.NODE_ENV === 'production' ?
            '/GDE3/datos1.xlsx' // Produccin
            :
            '/datos1.xlsx'; // Desarrollo

        const response = await fetch(defaultFilePath);
        const data = await response.arrayBuffer();
        const workbook = XLSX.read(data, { type: 'array' });
        const worksheet = workbook.Sheets[workbook.SheetNames[0]];
        excelDataTemp.value = XLSX.utils.sheet_to_json(worksheet);
        excelDataOriginal.value = [...excelDataTemp.value];


    };

    // Funcin para filtrar y analizar estudiantes
    const filterStudents = () => {
        const filteredItemsTemp = [];
        const graduatedStudents = {};
        const studentStatusMap = {};



        excelDataTemp.value.forEach((row) => {
            const termStarted = row['Term Started'];
            const startDateStr = store.getters.getTermDateMap[termStarted] || '';



            if (startDateStr) {
                const startDateObj = new Date(startDateStr);

                if (startDateObj >= initialDate && startDateObj <= finalDate) {
                    const item = {
                        Student: row['Student'],
                        Program: row['Program'],
                        Status: row['Status'],
                        'Term Started': termStarted,
                        'Start Date': startDateStr,
                    };


                    if (
                        item.Status !== 'Not Enrolled' &&
                        item.Status !== 'No Start' &&
                        (store.getters.getProgramsVersions[item.Program] || []).length > 0
                    ) {
                        const studentKey = `${item.Student}-${item.Program}-${item['Start Date']}`;

                        if (!studentStatusMap[studentKey]) {
                            studentStatusMap[studentKey] = {
                                item,
                                Graduated: false,
                                Dropped: false,
                                TransferredOut: false,
                                Enroll: false,
                            };
                        }

                        // Actualizar los estados segn el `Status`
                        if (item.Status === 'Graduated') {
                            studentStatusMap[studentKey].Graduated = true;
                            if (!graduatedStudents[item.Student]) {
                                graduatedStudents[item.Student] = [];
                            }
                            graduatedStudents[item.Student].push(item);
                        }
                        if (item.Status === 'Dropped') {
                            studentStatusMap[studentKey].Dropped = true;
                        }
                        if (item.Status === 'Transferred Out') {
                            studentStatusMap[studentKey].TransferredOut = true;
                        }
                        if (item.Status === 'Enroll') {
                            studentStatusMap[studentKey].Enroll = true;
                        }
                    }
                }
            }
        });



        // Clasificar los estudiantes segn sus estados
        Object.keys(studentStatusMap).forEach((studentKey) => {
            const status = studentStatusMap[studentKey];



            if (status.Graduated && status.TransferredOut) {
                filteredItemsTemp.push({...status.item, Status: 'Graduated' });
            } else if (status.Dropped && status.TransferredOut) {
                filteredItemsTemp.push({...status.item, Status: 'Dropped' });
            } else if (status.TransferredOut && status.Enroll) {
                filteredItemsTemp.push({...status.item, Status: 'Enroll' });
            } else if (status.Graduated) {
                filteredItemsTemp.push({...status.item, Status: 'Graduated' });
            } else if (status.Dropped) {
                filteredItemsTemp.push({...status.item, Status: 'Dropped' });
            } else if (status.Enroll) {
                filteredItemsTemp.push({...status.item, Status: 'Enroll' });
            }
        });


        // Asignar el resultado final a `filteredItems`
        filteredItems.value = filteredItemsTemp;
        console.log('Items despus del filtro:', filteredItems.value);
        console.log('Initial Date:', initialDate, 'Final Date:', finalDate);
    };

    return {
        loadExcel,
        filterStudents,
        filteredItems,
    };
}